import React, { useState } from "react"

const Index = () => {
  const overlays = [
    "factions",
    "lanes",
    "capitals",
    "compass",
    "notable",
    "regions",
  ]
  const [layer, setLayer] = useState(overlays[0])
  return (
    <div className="bottom-layer">
      <div className={`${layer} map-layer`}>
        <div className="button-row">
          <button
            className={layer === "factions" ? "selected" : ""}
            onClick={e => setLayer(overlays[0])}
          >
            Factions
          </button>
          <button
            className={layer === "lanes" ? "selected" : ""}
            onClick={e => setLayer(overlays[1])}
          >
            Hyperlanes
          </button>
          <button
            className={layer === "capitals" ? "selected" : ""}
            onClick={e => setLayer(overlays[2])}
          >
            Capitals
          </button>
          <button
            className={layer === "compass" ? "selected" : ""}
            onClick={e => setLayer(overlays[3])}
          >
            Compass
          </button>
          <button
            className={layer === "notables" ? "selected" : ""}
            onClick={e => setLayer(overlays[4])}
          >
            Notable
          </button>
          <button
            className={layer === "regions" ? "selected" : ""}
            onClick={e => setLayer(overlays[5])}
          >
            Regions
          </button>
        </div>
      </div>
    </div>
  )
}

export default Index
